.gameStarted {
  display: flex;
  height: 100%;
}

.game {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  width: 80%;
  text-align: center;
}

.gameCenter {
  display: flex;
  justify-content: space-between;
}

.playerHand, .compHand {
  overflow: auto;
  white-space: nowrap;
}

.output {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  padding: 20px;
  text-align: center;
}

.outputHidden {
}

.responseButton {
  cursor: pointer;
  color: white;
  background: rgba(116, 34, 34, 0.95);
  width: 6em;
  margin: 0 1em;
  padding: 0.3em 0;
  border: 0;
  border-radius: 0.2em;
}

.responseButton:hover {
  background-color: rgb(143, 32, 32);
}

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20%;
  border-left: 7px solid rgb(116, 34, 34);
  padding: 10px;
}

.deck {
  text-align: center;
}

.sidebarActions {
  text-align: center;
}

.sidebarActions > button {
  cursor: pointer;
  font-size: 1em;
  margin: 0.5em;
  border: 0;
  width: 10em;
  padding: 0.2em 0;
  color: white;
  background-color: rgba(116, 34, 34);
  border-radius: 0.2em;
}

.sidebarActions > button:hover {
  background-color: rgb(143, 32, 32);
}

#restart {
  background-color: rgb(190, 0, 0);
}

#restart:hover {
  background-color: rgb(233, 0, 0);
}

.card {
  height: 120px;
  cursor: pointer;
  margin: 2px;
}

.headings {
  color: white;
  margin: 10px 0;
}

.gameboardActions {
  position: absolute;
  color: white;
  margin: auto;
  width: 100%;
  text-align: center;
  top: 150px;
}

.gameboardActions > h1 {
  font-size: 3em;
}

.gameboardActions > button {
  cursor: pointer;
  font-size: 1.2em;
  margin: 0.5em;
  border: 0;
  width: 10em;
  padding: 0.5em 0;
  color: white;
  background-color: rgba(116, 34, 34);
  border-radius: 0.2em;
}

.gameboardActions > button:hover {
  background-color: rgb(143, 32, 32);
}

.gameOver {
  color: white;
}

#stat {
  text-decoration: underline;
}

.stats {
  color: white;
}