.gameboard {
  position: relative;
  border: 7px solid rgb(116, 34, 34);
  border-radius: 10px;
  margin: auto 10px;
  height: 500px;
  width: 1200px;
  background-color: rgb(0, 88, 0);
  font-size: 0.8em;
}

@media (min-height: 654px) {
  .gameboard {
    height: 600px;
    font-size: 1em;
  }  
}